*,*::before,*::after{margin:0;padding:0;box-sizing:inherit;list-style:none}html{font-size:62.5%}body{box-sizing:border-box;width:100%;margin:0;font-family:-apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, Courier New, monospace}.glcanvas{position:absolute;display:block;margin:0 auto;z-index:-10}

.gui-container{position:fixed;right:0px;width:300px;font-size:1.2rem;overflow-y:auto}.gui-container .scrollable{overflow-y:scroll}.gui-container::-webkit-scrollbar{width:8px}.gui-container::-webkit-scrollbar-track{background:#555}.gui-container::-webkit-scrollbar-thumb{background:#888}.gui-container::-webkit-scrollbar-thumb:hover{background:#4a4a4a}.gui-container .title{cursor:pointer;color:white;background:#1a1a1a url(data:image/gif;base64,R0lGODlhBQAFAJEAAP////Pz8////////yH5BAEAAAIALAAAAAAFAAUAAAIIlI+hKgFxoCgAOw==) 6px 8px no-repeat;padding-left:1.6rem;margin-left:-0.3rem}.gui-container .item{color:white;background:#1a1a1a;border-bottom:solid 1px rgba(0,0,0,0.3);padding:0rem 0.4rem}.gui-container .item:not(.folder){border-bottom:1px solid #2c2c2c}.gui-container .item label{padding-right:0.5rem;width:40%}.gui-container .item input{width:50%}.gui-container .item .btn-full{width:100%}.gui-container .item .btn-half{width:50%}.gui-container .group{display:block;opacity:1;height:auto;-webkit-transition:0.5s;transition:0.5s;-webkit-transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1);transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1);margin-left:0.3rem}.gui-container .group li:not(.folder){height:26px;line-height:26px}.gui-container .group .closed li:not(.title){display:none;opacity:0;height:0;margin:0px 1rem;overflow:hidden;-webkit-transition:0.5s;transition:0.5s;-webkit-transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1);transition-timing-function:cubic-bezier(0.05, 0.69, 0.14, 1)}.tooltip{display:inline-block;text-align:center;background-color:darkgrey;border-radius:50%;width:14px;height:14px;line-height:14px;cursor:default}.tooltip::before{content:'?';color:black;text-shadow:none}.tooltip span{visibility:hidden;max-width:400px;line-height:18px;text-align:center;background-color:rgba(0,0,0,0.95);color:#fff;padding:0.3rem 0.6rem;border-radius:0.3rem;position:absolute;z-index:1;margin:1rem}.tooltip:hover span{visibility:visible}.colormap{padding:0.4rem 0rem;height:100%;width:100%}

